.ProductPreview {
  flex-direction: column;
  display: flex;
  align-items: center;
  margin: 10px;
  padding: 10px;
  max-height: 500px;
  max-width: 400px;
  min-width: 50%;
  z-index: 1;
}

.ProductPreview_image {
  max-width: 250px;
  width: 100%;
  object-fit: contain;
}

.ProductPreview_line {
  margin-top: 5px;
  margin-bottom: 5px;
  font-weight: bold;
  width: 100%;
  margin-right: 0em;
}