.ProductDetail {
  display: flex;
  align-items: center;
  z-index: 1;
  width: 100%;
}

.ProductDetail_imagebox {
  flex-direction: column;
  display: flex;
  width: 100%;
  align-items: center;
}

.ProductDetail_imagebox_bottom{
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
}

.ProductDetail_font {
  margin: 5%;
  font-weight: bold;
}

.ProductDetail_slides {
  width: 100%;
  padding: 10%;
}

.slick-dots li button:before {
  font-size: 15px;
}

.slick-prev:before,
.slick-next:before {
  color: black;
}

.ProductDetail_image {
  width: 100%;
  object-fit: contain;
}

.ProductDetail_bottom {
  width: 100%;
}

.Prdouctdetail_name {
  text-align: left;
  margin: 5%;
}

.Prdouctdetail_info {
  margin: 7%;
  text-align: left;
  white-space: pre-line;
}