.header {
  display: block;
  width: 100%;
  height: 100%;
  position: sticky;
  top: 0;
  z-index: 100;
}

.header_topline {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  background-color: white;
  max-height: 100px;
}

.header_left_logo {
  width: 50%;
  max-width: 50px;
}

.header_middle {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  overflow: hidden;
  position: relative;
}

.header_middle_logo {
  width: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: 0;
  transform: translate(-50%, -50%);
}

.header_banner {
  width: 100%;
  height: 100%;
  max-height: 170px;
  overflow: hidden;
}

.header_banner_image {
  width: 100%;
}

@media (prefers-reduced-motion: no-preference) {
  .header_left_logo {
    animation: header_left_logo_spin infinite 20s linear;
  }
}

@keyframes header_left_logo_spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}